//引顶部返回组件
import actionbar from "@/components/actionbar/actionbar.vue"
const app = getApp()
export default {
	data() {
		return {
			statusBar:"",
			accounttext:"",
			accountlist:[]
		}
	},
	components:["actionbar"],
	onLoad(){
		this.statusBar = app.globalData.statusBar
	},
	created() {
		const _this = this
		uni.getStorage({
			key:"bindtokey",
			success(res) {
				// console.log(res.data)
				uni.request({
					url:`${app.globalData.Requestpath}user/getUserDetail`,
					method:"POST",
					data:{
						token:res.data
					},
					success(resaccount) {//获取账户详情
						if(resaccount.data.code==0){
							_this.accounttext = resaccount.data.data.user_amount
						}
					}
				})
				uni.request({
					url:`${app.globalData.Requestpath}user/getBalanceLogList`,
					method:"POST",
					data:{
						token:res.data,
						page:1,
						pageSize:10
					},
					success(resbalance) {
						if(resbalance.data.code==0){
							_this.accountlist = resbalance.data.data.list
							console.log(_this.accountlist)
						}
					}
				})
			}
		})
	}
}
